import React from 'react';

import Egg from './images/egg.png'
import Egg2 from './images/egg2.png'
import Egg3 from './images/egg3.png'
import Egg4 from './images/egg4.png'
import Egg5 from './images/egg5.png'
import Header from './images/header.png'
import './css/main.css'

class App extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			egg: Egg5
		}
	}
	count = () => {
		let th = this;
		const second = 1000,
			minute = second * 60,
			hour = minute * 60,
			day = hour * 24;
		const eggSource = [
			Egg, Egg2, Egg3, Egg4, Egg5
		]
		let eggCur = Egg;
		this.setState({
			egg: Egg5
		})
		eggCur = eggSource[4];

		let countDown = new Date('Oct 15, 2020 00:00:00').getTime(),

			x = setInterval(function () {
				let now = new Date().getTime();
				let distance = countDown - now;

				document.getElementById('days').innerText = Math.floor(distance / (day));
				document.getElementById('hours').innerText = Math.floor((distance % (day)) / (hour));
				document.getElementById('minutes').innerText = Math.floor((distance % (hour)) / (minute));

				if (Math.floor(distance / (day)) >= 29) {
					eggCur = eggSource[0]
					th.setState({ egg: eggCur })
				}
				if (Math.floor(distance / (day)) <= 25) {
					eggCur = eggSource[1]
					th.setState({ egg: eggCur })
				}
				if (Math.floor(distance / (day)) <= 18) {
					eggCur = eggSource[2]
					th.setState({ egg: eggCur })
				}
				if (Math.floor(distance / (day)) <= 11) {
					eggCur = eggSource[3]
					th.setState({ egg: eggCur })
				}
				if (Math.floor(distance / (day)) <= 4) {
					eggCur = eggSource[4]
					th.setState({ egg: eggCur })
				}
			}, second)

	}


	componentDidMount() {
		this.count()
	}
	render() {
		return (
			<div className="content">
				<img className="header" src={Header} />
				<div className="eggContainer">
					<img src={this.state.egg} />
				</div>
				<ul className="countDown">
					<li><span id="days"></span>дней</li>
					<li><span id="hours"></span>часов</li>
					<li><span id="minutes"></span>минут</li>
				</ul>
			</div>
		);
	}
}

export default App;
